/*============================================================================
	#POPUP
==============================================================================*/
.site__popup {
  display: none; }

.popup-close {
  width: 40px;
  height: 40px;
  background: #bce4e5;
  border-radius: 100%;
  display: flex;
  flex: 0 0 auto;
  justify-content: center;
  align-items: center;
  border: 0;
  padding: 0;
  line-height: 1;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.15s; }
  .popup-close span {
    margin-top: -2px;
    margin-right: -1px; }
  .popup-close:hover {
    background: #f2d2d5; }

.popup-login {
  max-width: 940px;
  margin: 0 auto;
  background: #bce4e5; }
  .popup-login__container {
    position: relative; }
    .popup-login__container::after {
      content: '';
      position: absolute;
      bottom: -58px;
      left: 0;
      width: 100%;
      height: 60px;
      background: url("../images/popup-layer-deco.png") no-repeat; }
  .popup-login__header {
    padding: 12px 40px;
    background: white;
    display: flex;
    position: relative;
    align-items: center; }
  .popup-login__logo {
