    margin-right: 40px;
    color: #323232;
    margin-top: 8px; }
  .popup-login__body {
    display: flex;
    justify-content: space-between;
    padding: 46px 40px 110px; }
  .popup-login__form {
    margin-right: 65px;
    flex: 1; }
  .popup-login .popup-form {
    margin-bottom: 50px; }
  .popup-login__form-title {
    font-family: "Amatic SC", cursive;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.2;
    letter-spacing: 0.08em;
    margin-top: -8px;
    margin-bottom: 5px; }
  .popup-login .form__row {
    margin-bottom: 20px; }
  .popup-login .form__paswrd-rst-link {
    text-decoration: none;
    text-transform: uppercase;
    color: #818181;
    display: inline-block;
    position: relative;
    font-size: 10px;
    margin-left: 30px; }
    .popup-login .form__paswrd-rst-link::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      background: #818181;
      bottom: 0px;
      left: 0; }
  .popup-login .form__button {
    width: 174px;
    text-align: center; }
  .popup-login__reg-container {
    position: relative;
    padding: 0 45px;
    text-align: center;
    z-index: 1;
    display: inline-block; }
    .popup-login__reg-container::before {
      content: '';
      position: absolute;
      top: calc(50% + 20px);
      left: 50%;
      transform: translate(-50%, -50%);
      width: 292px;
      height: 93px;
      background: url("../images/popup-reg-deco.svg") no-repeat;
      background-position: 100% 100%;
      background-size: 100%;
      z-index: -1; }
  .popup-login__reg-title {
    font-family: "Amatic SC", cursive;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 0.08em;
