/*============================================================================
	#Icons
==============================================================================*/

.icon {

	height: 30px;
	width: 30px;
	transition: fill 0.3s ease;
	cursor: pointer;

	&._lock {
		width: 15px;
		height: 19px;
	}

	&._search {
		width: 20px;
		height: 18px;
	}
	&._arrow-right {
		width: 28px;
		height: 20px;
	}
	&._arrow-next-pag,
	&._arrow-prev-pag {
		width: 38px;
		height: 20px;
	}
}
